
/* 登录头部 */
.head{
    margin: 12px auto 14px;
    width: 1200px;
    height: 60px;
    img{
        float: left;
        margin: 7px 0 0 0;
    }
    .improve{
        float: right;
        margin-top: 34px;
        color: #787878;
        cursor: pointer;
        span{
            display: inline-block;
            width: 15px;
            height: 19px;
            vertical-align: middle;
        }
    }:hover{
        color: #6e6e6e;
        text-decoration: underline;
    }
}

/* 登录主体 */
.area{
    width: 1200px;
    min-height: 400px;
    margin:0 auto;
}
.wrap{
    width: 362px;
    margin: 0 auto;
    color: #6e6e6e;
    color: #3c3c3c;
    position: absolute;
    left: 40%;
    z-index: 10;
    .notice{
        width: 360px;
        height: 24px;
        line-height: 24px;
        background: url(https://login.dangdang.com/images/new_tip.png) 3px 5px no-repeat #fef7f5;;
        color: #828282;
        border: 1px solid #fedbd1;
        text-indent: 20px;
        a{
            color: #1a66b3;
        }
    }
}
.wrap_con{
    border: 1px solid #eef2f4;
    box-shadow: 0px 1px 2px #eee;
    padding: 15px 25px 15px 15px; 
    background: #fff;
    .password_login{
        font-size: 18px;
        color: #3c3c3c;
        margin-top: 5px;
        margin-bottom: 20px;
    }
    .username{
        height: 36px;
        border: 1px solid #e6e6e6;
        width: 310px;
        border-radius: 2px;
        margin-bottom: 30px;
        span{
            width: 30px;
            margin-right: 10px;
            height: 36px;
            float: left;
            background: url(../img/icon_20150706.png) 10px -110px no-repeat;
        }
        input{
            width: 270px;
            height: 34px;
            float: left;
            line-height: 34px;
            border: 0;
            // color: rgb(176, 176, 176);
            padding: 0;
            background-color: none;
        }
    }
    .password{
        @extend .username;
        span{
            background-position: 10px -150px;
        }
    }

    .auto_login{
        width: 310px;
        height: 27px;
        line-height: 22px;
        padding-top: 7px;
        overflow: hidden;
        input{
            vertical-align: middle;
            margin-top: -1px;
        }
        a{
            float: right;
        }
    }

    .login_btn{
        border: none;
        width: 100%;
        border-radius: 5px;
        color: #fff;
        display: inline-block;
        height: 44px;
        text-align: center;
        font-size: 20px;
        line-height: 44px;
        background: #ff2832;
        cursor: pointer;
    }
    .register{
        height: 24px;
        line-height: 24px;
        padding-top: 6px;
        span{
            margin-right: 50px;
        }
        .liji{
            float: right;
            color: #6e6e6e;
        }
        .liji:hover{
            color: #505050;
            text-decoration: underline;
        }
    }
    .partner{
        height: 22px;
        li{
            float: left;
            margin-right: 5px;
            a{
                width: 20px;
                height: 20px;
                display: block;
                background: url(../img/icon_20150706.png) 0 -260px no-repeat;
                text-indent: 99em;
                overflow: hidden;
            }
        }
        @for $i from 1 through 3 {
            $index:1+$i;
            li:nth-of-type(#{$index}) a{
                background-position: 0 -260px-20px*$i;
            }
        }
        .more{
            padding-left: 10px;
            position: relative;
            float: left;
            line-height: 20px;
            a{
               color: #6e6e6e;
            }
            a:hover{
                color: #787878;
            }
        }
    }
}
.bd{
    width: 100%;
    height: 500px;
    background: url('http://img62.ddimg.cn/upload_img/00684/zn/bc1-1584595558.jpg');
    background-size: cover;
    padding-top: 50px;
    .cup{
        width: 1051px;
        height: 229px;
        background: url(http://img62.ddimg.cn/upload_img/00684/zn_yf/cup-1584599390.png) no-repeat center top;
        position: absolute;
        animation: assp 3s infinite;
        transition: all 2s;
    }
    .girls{
        width: 1069px;
        height: 432px;
        background: url(http://img63.ddimg.cn/upload_img/00684/zn_yf/music-1584599390.png) no-repeat center top;
        position: absolute;
        top: 87px;
        left: 20%;
        animation: asp 2.5s infinite;
        transition: all 1s;
    }
}
@keyframes asp{
    0% {
        left: 110px;
    }
    50% {
        left: 120px;
    }
    100% {
        left: 110px;
    }
}
@keyframes assp{
    0% {
        left: 210px;
        margin-top: 100px;
    }
    50% {
        left: 200px;
        margin-top: 95px;
    }
    100% {
        left: 210px;
        margin-top: 100px;
    }
}
@import "dlogin_footer";

