.login_footer{
    color: #8c8c8c;
    font-size: 12px;
    text-decoration: none;
    border-top: 1px solid #ebebeb;
    width: 100%;
    padding: 17px 0 50px;
    .footer_nav{
        width: 960px;
        margin: 0 auto;
        text-align: center;
        line-height: 20px;
        padding-bottom: 17px;
        padding-top: 25px;
        a{
            color: #8c8c8c;
            font-size: 12px;
        }:hover{
            color: #f48570;
            text-decoration: underline;
        }
        span{
            margin: 0 9px 0 2px;
        }
    }
    .footer_copyright{
        width: 960px;
        margin: 0 auto;
        line-height: 20px;
        text-align: center;
        margin-bottom: 10px;
    }
    .footer_icon{
        padding: 10px 0 0 240px;
        width: 640px;
        margin: 0 auto;
        height: 77px;
        img{
            float: left;
            width: 135px;
            height: 47px;
            padding: 15px 5px 15px;
            cursor: pointer;
        }
    }
}
